import React, { useContext } from 'react';
import {LoginContext} from '../contexts/LoginContext';


function ProductDetailsComponent (props) {
    const { cart, setCart } = useContext(LoginContext);
    return (
        
        <div className="card" style={{width: "18 rem"}}>
            <img src={props.product.imageUrl} className="card-img-top" alt="item"></img>
            <div className="card-body ">
                <h5 className="card-title">{props.product.title}</h5>
                <p className="card-text">{props.product.description}</p>
                <p className="card-text">Price: {props.product.price}</p>
                <p className="card-text">Items in stock: {props.product.quantity}</p>
                <button className="btn btn-primary" onClick={()=>setCart(cart+1)}>Add to cart</button>
                {/* <button className="btn btn-primary" onClick={() => this.history.push("/")}>Back to product list</button> */}
                <a href={props.buttonLink}  className="mx-5 btn btn-success" >{props.button}</a>
            </div>
        </div>
    )
}
export default ProductDetailsComponent;

